body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.arrow-down{
    display: inline-block;
    margin-left: .255em;
    vertical-align: .255em;
    content: "";
    border-top: .3em solid;
    border-right: .3em solid transparent;
    border-bottom: 0;
    border-left: .3em solid transparent;
}
.dropdown{
    display: inline-flex;
    margin-left: 10px;
}
.dropdown-button{
    align-items: center;
    background-color: rgb(102, 102, 102);
    cursor: pointer;
    display: flex;
    font-weight: bold;
    justify-content: center;
    height: 30px;
    width: 100px;
}
.dropdown-item{
    margin-right: 3px;
}
.dropdown-list{
    background-color: black;
    display: inline-block;
    font-size: 15px;
    max-width: 150px;
    min-width: 100px;
    padding: 5px;
    position: absolute;
    animation: drop-list .5s ease-in-out;
    -webkit-animation: drop-list .5s ease-in-out;
    -moz-animation: drop-list .5s ease-in-out;
    -o-animation: drop-list .5s ease-in-out;
}
.dropdown-list-item{
    cursor: pointer;
}
.flags{
    margin-right: 10px;
    margin-bottom: 10px;
}
.flag-clicked{
    opacity: 0.5;
}
.flag-icon{
    border: 2px solid gray;
    border-radius: 4px;
    height: 15px;
    margin-left: 10px;
    width: 25px;
}
.flag-icon:hover{
    border-color: black;
    cursor: pointer;
    opacity: 0.5;
}
header{
    align-items: flex-end;
    background-color: rgb(204, 204, 204);
    display: flex;
    height: 105px;
    justify-content: space-between;
    margin-top: 10px;
}

@media only screen and (max-width: 999px){
    header{
        align-items: flex-start;
        flex-direction: column-reverse;
        justify-content: space-around;
    }
}

@media only screen and (max-width: 442px){
    .dropdown-button{
        width: 80px;
    }
    .flags{
        margin-left: auto;
        margin-right: auto;
    }
}

@keyframes drop-list {
    0%   { 
        -webkit-transform-origin: left top; 
                transform-origin: left top;
        -webkit-transform: scaleY(0);
                transform: scaleY(0); 
        }
    100% { 
        -webkit-transform-origin: left top; 
                transform-origin: left top;
        -webkit-transform: scaleY(1);
                transform: scaleY(1); 
        }
  }

@-webkit-keyframes drop-list {
    0%   { 
        -webkit-transform-origin: left top; 
                transform-origin: left top;
        -webkit-transform: scaleY(0);
                transform: scaleY(0); 
        }
    100% { 
        -webkit-transform-origin: left top; 
                transform-origin: left top;
        -webkit-transform: scaleY(1);
                transform: scaleY(1); 
        }
    }
#loading{
    color: tomato;
    margin-top: 20px;
    text-align: center;
}
.page-link{
    cursor: pointer;
}
.pagination{
    font-family: monospace;
    font-size: 18px;
    text-align: center;
    margin-bottom: 10px;
    margin-top: 10px;
}
.table{
    border-collapse: collapse;
    display: block;
    height: 700px;
    overflow: auto;
}
th, td {
    border-bottom: 1px solid #ddd;
    text-align: center;
}
#table-header{
    background-color: rgb(81, 81, 81);
    height: 35px;
}
tr:nth-child(odd){
    background-color: rgb(163, 163, 163);
}

@media only screen and (max-width: 510px){
    .pagination{
        font-size:13px;
    }
}
@font-face{
    font-family: Helvetica-Inserat;
    src: url("/react-page/static/media/Helvetica Inserat LT.b12ec7ca.ttf");
}
#horizontal-line{
    background-color: black;
    border: 0px;
    box-shadow: 0.2px 0.2px;
    height: 1px;
}
.left-column{
    border: 10px solid rgb(204, 204, 204);
    width: 70%;
    padding: 10px;
}
.paragraph{
    margin-left: 15px;
    margin-right: 15px;
    text-indent: 35px;
}
#title{
    font-family: Helvetica-Inserat;
    margin-left: 28px;
}

@media only screen and (max-width: 999px){
    .left-column{
        width: unset;
    }
}

@-moz-document url-prefix() {
    #horizontal-line {
      box-shadow: 1px 1px;
    }
}

.arrow{
    float: right;
    margin-right: 4px;
}
.arrow-down-right{
    display: inline-block;
    margin-left: .255em;
    margin-top: 6px;
    vertical-align: .255em;
    content: "";
    border-top: .3em solid;
    border-right: .3em solid transparent;
    border-bottom: 0;
    border-left: .3em solid transparent;
}
.arrow-up{
    display: inline-block;
    margin-left: .255em;
    margin-top: 4px;
    content: "";
    border-top: .3em solid transparent;
    border-right: 0;
    border-bottom: .3em solid transparent;
    border-left: .3em solid;
}
.content{
    animation: fadeInDown 2s ease-in-out;
    -webkit-animation: fadeInDown 2s ease-in-out;
    -moz-animation: fadeInDown 2s ease-in-out;
    -o-animation: fadeInDown 2s ease-in-out;
    background-color: black;
    overflow: hidden;
    padding: 20px 15px 20px 15px;
}
.right-column{
    border: 10px solid rgb(163, 163, 163);
    padding: 10px;
    width: 30%; 
}
.link{
    background-color: black;  
    cursor: pointer;
    margin-top: 3px;
    padding: 6px 15px 6px 15px;
}

@keyframes fadeInDown {
    from {
        max-height: 5px;
    }
    to {
        max-height: 220px;
    }
  }

@-webkit-keyframes fadeInDown {
    from {
        max-height: 5px;
    }
    to {
        max-height: 220px;
    }
  }

@media only screen and (max-width: 999px){
    .right-column{
        width: unset;
    }
}

.main{
    background-color: rgb(81, 81, 81);
    display: flex;
    margin-bottom: 15px;
    margin-top: 15px;
}

@media only screen and (max-width: 999px){
    .main{
        flex-direction: column;
    }
}
#footer{
    align-items: center;
    background-color: rgb(204, 204, 204);
    color: rgb(120, 120, 120);
    display: flex;
    height: 40px;
    justify-content: space-between;
    margin-bottom: 15px;
}
#footer-info{
    margin-left: 20px;
}
.footer-links-item{
    color: rgb(120, 120, 120);
    margin-right: 20px;
}

@media only screen and (max-width: 900px){
    #footer{
        flex-direction: column;
        height: unset;
    }
    .footer-links{
        margin-top: 10px;
        text-align: center;
    }
}
.App{
    color: white;
    font-family: Tahoma, sans-serif;
    max-width: 1280px;
    min-width: 800px;
    margin: auto;
}
#logo{
    margin-top: 85px;
    position: fixed;
    width: 150px;
    z-index: 2;
}

@media only screen and (max-width: 999px){
    .App{
        max-width: unset;
        min-width: unset;
    }
    #logo{
        display: none;
    }
}
